@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700&display=swap');

.timer-container {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-size: 100%;
}

.timer {
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    padding-top: 2em;
    padding-bottom: 3em;
}

.timer div {
    border: 1px solid #fff;
    border-radius: 5px;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    padding: 3em;
    background: rgba(255, 255, 255, 0.05);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.6);
    transition: box-shadow 0.5s ease-in-out;
}

.timer div:hover {
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.9);
}

.HENRY p:first-child, .timer div span {
    font-size: 3.5em;
    font-family: 'Orbitron', sans-serif;
    color: rgb(233, 113, 33);
    text-shadow: 0 4px 6px rgba(249, 179, 132, 1);
    transition: color 0.3s ease-in-out, text-shadow 0.3s ease-in-out;
}

.timer div span:hover, .HENRY p:first-child:hover {
    color: #f9b384;
    text-shadow: 0 4px 20px rgba(249, 179, 132, 1);
}

.HENRY p small {
    color: #fff;
    font-family: 'Orbitron', sans-serif;
    font-size: 1.2em;
    text-transform: uppercase;
    letter-spacing: 1px;
}

@keyframes pulse {
    0%, 100% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
}

.HENRY p:first-child {
    animation: pulse 2s infinite;
}

@media screen and (max-width: 480px) {
    .timer div {
        margin-left: 1em;
        margin-right: 1em;
        padding: 2em 1em;
    }

    .HENRY p:first-child, .timer div span {
        font-size: 2.5em;
    }
}
